<template>
  <div id="main-container">
    <h1 class="ui-overlay" :class="[lightUi ? 'white' : 'black']">{{ hexValue }}</h1>
    <touchpad :light-ui="lightUi"></touchpad>
    <div id="swatches-container">
      <swatch
        v-for="singleColor in this.$parent.savedColors"
        :key="singleColor.id"
        :color="singleColor.color"
      ></swatch>
    </div>
  </div>
</template>

<script>
import * as cLib from "./colorConversion.js";
import Touchpad from "./Touchpad.vue";
import Swatch from "./Swatch.vue";

export default {
  computed: {
    hexValue: function() {
      return cLib.hsvToRgb(this.$parent.color, true);
    },
    lightUi: function() {
      return cLib.findColorLightness(this.$parent.color) < 0.1791;
    }
  },
  components: {
    Touchpad,
    Swatch
  }
};
</script>

<style>
#swatches-container {
  position: absolute;
  top: 0;
  right: 0;
  box-sizing: border-box;
  width: 300px;
  height: 100vh;
  padding-top: 2rem;
  background-color: #222;
  overflow-y: scroll;
}
</style>
