import * as cLib from "./colorConversion";
import Vue from "vue";
import App from "./App.vue";
import './style.css';

var color = {
    h: Math.floor(Math.random() * 360),
    s: Math.random(),
    v: 1
  },
  locked = false,
  savedColors = [],
  colorId = 0;

var vm = new Vue({
  el: "#app",
  render: h => h(App),
  data: {
    color: color,
    savedColors: savedColors
  },
  computed: {
    lightUi: function() {
      // use light UI color when lightness is below treshold
      return cLib.findColorLightness(color) < 0.1791;
    }
  },
  mounted: function() {
    // attach listeners to touchpad when app is mounted
    var el = document.getElementById("scrollTarget");
    el.addEventListener("wheel", scrollHandler, false);
    el.addEventListener("mousemove", hueSatHandler, false);
  }
});

updateBackgroundColor();

// Handling copying and saving of color values
document.addEventListener(
  "keydown",
  event => {
    // locking input with space
    if (event.keyCode == 32) {
      locked = !locked;
      event.preventDefault();

      // saving to savedColors
    } else if (event.key == "s" && (event.ctrlKey || event.metaKey)) {
      savedColors.push({
        id: colorId++,
        color: Object.assign({}, color)
      });
      event.preventDefault();
    }
  },
  false
);

// Copying copies current color hex value to clipboard
document.addEventListener(
  "copy",
  event => {
    let hex = getColorHexString();
    event.clipboardData.setData("text/plain", hex);
    console.log("Copied to clipboard");
    event.preventDefault();
  },
  false
);

function scrollHandler(event) {
  event.preventDefault();
  if (locked) return; // do nothing if color is locked
  setColorValue(event.deltaY); // updates color value acording to scroll
  updateBackgroundColor();
}

function hueSatHandler(event) {
  if (locked) return;
  let yPos = event.offsetY / event.target.offsetHeight;
  let xPos = event.offsetX / event.target.offsetWidth;

  color.h = Math.floor(xPos * 360);
  color.s = 1 - yPos;

  updateBackgroundColor();
}

// Sets color HSV value (0 to 1 inclusive) based on mouse scroll
function setColorValue(deltaY) {
  if (deltaY == null) return;
  var changeValue = deltaY * 0.0006;
  color.v = Math.min(1, Math.max(0, color.v + changeValue));
  vm.color = color;
}

// Updates the background color and shows hex value in the h1
function updateBackgroundColor() {
  let bg = document.getElementsByTagName("body")[0];
  bg.style.backgroundColor = getColorHexString();
}

// Gets current color as a hex string
function getColorHexString(hash = true) {
  let pre = hash ? "" : "";
  return pre + cLib.hsvToRgb(color, true);
}
